'use client';
import React from 'react';
import Link from 'next/link';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';

const Header = () => {
  return (
    <Box 
      component="header"
      sx={{ 
        backgroundColor: '#1b5e20',
        position: 'sticky',
        top: 0,
        zIndex: 1300,
        boxShadow: '0px 2px 4px rgba(0,0,0,0.2)',
      
      }}
    >
      <Toolbar>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, color: 'white' }}>
          Logo
        </Typography>
        <Box sx={{ display: 'flex', gap: 3, mr: 10 }}>
          <Button color="inherit" component={Link} href="/HeroSection" sx={{ color: 'white' }}>
            Home
          </Button>
          <Button color="inherit" component={Link} href="/about" sx={{ color: 'white' }}>
            About
          </Button>
          <Button color="inherit" component={Link} href="/contact" sx={{ color: 'white' }}>
            Contact
          </Button>
        </Box>
      </Toolbar>
    </Box>
  );
};

export default Header;
