'use client';
import React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import { facialFeatures, analysisSummary } from '@/utils/appData';

const FacialAnalysisFeatures = () => {
  const getScoreColor = (score) => {
    if (score >= 8) return 'success';
    if (score >= 6) return 'warning';
    return 'error';
  };

  const getScoreText = (score, type = 'feature') => {
    if (type === 'percentage') return `${score}%`;
    if (type === 'severity') {
      if (score <= 3) return 'Low';
      if (score <= 6) return 'Moderate';
      return 'High';
    }
    return `${score}/10`;
  };

  return (
    <Box sx={{ py: 4, backgroundColor: 'background.paper' }}>
      <Container maxWidth="lg">
        {/* Header Section */}
        <Box sx={{ textAlign: 'center', mb: 6 }}>
          <Typography variant="h3" component="h2" gutterBottom sx={{ fontWeight: 'bold' }}>
            AI Facial Analysis Features Results
          </Typography>
          <Typography variant="h5" color="text.secondary" sx={{ maxWidth: 600, mx: 'auto', mb: 2 }}>
            Detailed analysis of your facial features with personalized improvement suggestions
          </Typography>
          
          {/* Summary Chip */}
          <Stack direction="row" spacing={2} justifyContent="center" sx={{ mb: 3 }}>
            <Chip 
              label={`Face Shape: ${analysisSummary.faceShape}`} 
              color="primary" 
              variant="outlined"
            />
            <Chip 
              label={`Overall Score: ${analysisSummary.overallScore}/10`} 
              color="primary"
            />
          </Stack>
        </Box>

        {/* Features Grid */}
        <Grid container spacing={3}>
          {facialFeatures.map((feature, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card 
                sx={{ 
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  transition: 'transform 0.3s ease-in-out',
                  '&:hover': {
                    transform: 'translateY(-8px)',
                    boxShadow: 4,
                  },
                }}
              >
                <CardContent sx={{ p: 3, flexGrow: 1 }}>
                  {/* Icon and Title */}
                  <Box sx={{ mb: 2, display: 'flex', justifyContent: 'center' }}>
                    {feature.icon}
                  </Box>
                  
                  <Typography variant="h6" component="h3" gutterBottom sx={{ fontWeight: 'bold', textAlign: 'center' }}>
                    {feature.title}
                  </Typography>
                  
                  {/* Score Display */}
                  {feature.score !== null && (
                    <Box sx={{ mb: 2, textAlign: 'center' }}>
                      <Chip 
                        label={getScoreText(
                          feature.score, 
                          feature.details.measurement || 'feature'
                        )}
                        color={getScoreColor(feature.score)}
                        size="small"
                        sx={{ fontWeight: 'bold' }}
                      />
                    </Box>
                  )}
                  
                  {/* Description */}
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 2, textAlign: 'center' }}>
                    {feature.description}
                  </Typography>
                  
                  {/* Suggestions */}
                  <Box sx={{ mt: 2, p: 2, bgcolor: 'action.hover', borderRadius: 1 }}>
                    <Typography variant="subtitle2" sx={{ fontWeight: 'bold', mb: 1 }}>
                      Suggestion:
                    </Typography>
                    <Typography variant="body2">
                      {feature.details.suggestion}
                    </Typography>
                    
                    {/* Improvement Tips */}
                    {feature.details.improvementTips && (
                      <Box sx={{ mt: 2 }}>
                        <Typography variant="subtitle2" sx={{ fontWeight: 'bold', mb: 1 }}>
                          Quick Tips:
                        </Typography>
                        <ul style={{ margin: 0, paddingLeft: '16px' }}>
                          {feature.details.improvementTips.slice(0, 2).map((tip, idx) => (
                            <li key={idx}>
                              <Typography variant="body2" color="text.secondary">
                                {tip}
                              </Typography>
                            </li>
                          ))}
                        </ul>
                      </Box>
                    )}
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

    

        {/* Footer Note */}
        <Typography 
          variant="body2" 
          color="text.secondary" 
          sx={{ 
            textAlign: 'center', 
            mt: 4, 
            fontStyle: 'italic' 
          }}
        >
          
        </Typography>
      </Container>
    </Box>
  );
};

export default FacialAnalysisFeatures;